<template>
  <div class="commentIndex">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-table
      :data="comments"
      style="width: 100%"
      v-loading="isLoading"
      >
      <el-table-column
        prop="title"
        label="">
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="">
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.comment_status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            :disabled="scope.row.loading"
            @change="changeCommentStatus(scope.row)"
            >
          </el-switch>
        </template>
      </el-table-column>
    </el-table>
    <div style="height:30px"></div>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-sizes="[10, 20, 30, 50]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalCount"
      background
      :disabled="paginationDisabled"
      >
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
// 
import { getArticles, editCommentStatus } from '@/api/article'
export default {
  name: 'commentIndex',
  data () {
    return {
      comments: [], // 
      totalCount: 0, // 
      currentPage: 1, // 
      pageSize: 10, // 
      isLoading: false,
      paginationDisabled: false
    }
  },
  created () {
    this.loadComment(1)
  },
  methods: {
    // 
    loadComment (page) {
      this.paginationDisabled = true
      this.isLoading = true
      this.currentPage = page
      getArticles({
        response_type: 'comment',
        page,
        per_page: this.pageSize
      }).then(res => {
        this.paginationDisabled = false
        this.isLoading = false
        const results = res.data.data.results
        results.forEach(item => {
          item.loading = false
        })
        this.comments = results
        this.totalCount = res.data.data.total_count
      })
    },
    // 
    changeCommentStatus (item) {
      item.loading = true
      editCommentStatus(
        item.id.toString(), item.comment_status
      ).then(res => {
        item.loading = false
      })
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.loadComment(1)
    },
    // 
    handleCurrentChange (val) {
      this.currentPage = val
      this.loadComment(this.currentPage)
    }
  }
}
</script>

