<template>
  <div class="login-container">
    <el-form ref="form" :model="user" :rules="formRule" class="loginForm">
      <el-form-item>
        <div class="logo"></div>
      </el-form-item>
      <el-form-item prop="mobile">
        <el-input v-model="user.mobile" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input v-model="user.code" placeholder=""></el-input>
      </el-form-item>
      <el-form-item  prop="agree">
         <el-checkbox v-model="user.agree"></el-checkbox>
      </el-form-item>
      <el-form-item>
         <el-button type="primary" :loading="loginLoad" @click="login" class="loginBtn"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// 
import { userLogin } from '@/api/user'
export default {
  name: 'login',
  data () {
    return {
      user: {
        mobile: '13911111111',
        code: '246810',
        agree: true
      },

      // loading
      loginLoad: false,

      // 
      formRule: {
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3|5|6|7|8|9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{6}$/, message: '', trigger: 'blur' }
        ],
        agree: [
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    login () {
      // 
      this.$refs.form.validate(valid => {
        if (!valid) {
          return
        }
        this.onLogin()
      })
    },
    onLogin () {
      // loading
      this.loginLoad = true
      // 
      userLogin(this.user).then(res => {
        // TODO:
        // 
        this.$message({
          message: '',
          type: 'success'
        })

        // loading
        this.loginLoad = false

        // token
        window.localStorage.setItem('user', JSON.stringify(res.data.data))

        // 
        this.$router.push('/')
      }).catch(() => {
        // TODO:

        // 
        this.$message.error(',')

        // loading
        this.loginLoad = false
      })
    }
  }
}
</script>

<style scoped lang="less">
  .login-container {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: url('./background-img2.jpg') no-repeat;
      background-size: cover;
      display: flex;
      justify-content: center;
      align-items: center;
      .loginForm {
        padding: 50px;
        background-color: #fff;
        min-width: 300px;
      }
      .loginBtn {
          width: 100%;
      }
      .logo {
          width: 259px;
          height: 57px;
          background: url('./logo_index.png') no-repeat;
      }
  }
</style>
