<template>
  <div class="publish">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item to="/"></el-breadcrumb-item>
                <el-breadcrumb-item>{{$route.query.id ? '' : ''}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-form
        ref="publish-form"
        :model="article"
        label-width="60px"
        :rules="publishRules"
        >
          <el-form-item label="" prop="title">
            <el-input v-model="article.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-tiptap
            :extensions="extensions"
            v-model="article.content"
            height="350"
            ></el-tiptap>
          </el-form-item>
           <el-form-item label="">
            <el-radio-group v-model="article.cover.type">
              <el-radio :label="1"></el-radio>
              <el-radio :label="3"></el-radio>
              <el-radio :label="0"></el-radio>
              <el-radio :label="-1"></el-radio>
            </el-radio-group>
            <template v-if="article.cover.type > 0">
              <upload-cover
              v-for="(i, index) in article.cover.type"
              :key="i"
              class="cover-container"
              v-model="article.cover.images[index]"
              ></upload-cover>
            </template>
          </el-form-item>
          <el-form-item label="" prop="channel_id">
            <el-select v-model="article.channel_id" placeholder="">
              <el-option
              :label="item.name"
              :value="item.id"
              v-for="item in channels"
              :key="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onPublish(false)">{{$route.query.id ? '' : ''}}</el-button>
            <el-button @click="onPublish(true)"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
  </div>
</template>

<script>
// 
import UploadCover from './components/upload-cover'
// 
import {
  getArticleChannels,
  addArticle,
  getArticle,
  editArticle
} from '@/api/article.js'
import { uploadImage } from '@/api/image.js'
// 
import {
  ElementTiptap,
  //  extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Image
} from 'element-tiptap'

// 
import 'element-tiptap/lib/index.css'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  data () {
    return {
      channels: [], // 
      article: { // 
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: ''
      },
      // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Underline(),
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new Image({
          //  base64 
          uploadRequest (file) {
            //  Content-Type  multipart/form-data FormData
            const fd = new FormData()
            fd.append('image', file)
            // 1 return  Promise 
            //  axios  Promise 
            return uploadImage(fd).then(res => {
              //  return 
              return res.data.data.url
            })
          }
        })
      ],
      // 
      publishRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          {
            validator: (rule, value, callback) => {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  created () {
    this.getChannels()
    if (this.$route.query.id) {
      this.getArticleInfo()
    }
  },
  methods: {
    onPublish (draft) {
      // 
      this.$refs['publish-form'].validate((valid) => {
        // ,
        if (!valid) {
          return
        }
        // ,
        if (this.$route.query.id) {
          editArticle(this.$route.query.id, this.article, draft).then(res => {
            // if (draft) {
            //   this.$message('')
            // } else {
            //   this.$message('')
            // }
            // draft ? this.$message('') : this.$message('')
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
            this.$router.push('/article')
          })
        } else {
          addArticle(this.article, draft).then(res => {
            // if (draft) {
            //   this.$message('')
            // } else {
            //   this.$message('')
            // }
            // draft ? this.$message('') : this.$message('')
            this.$message({
              message: draft ? '' : '',
              type: 'success'
            })
            this.$router.push('/article')
          })
        }
      })
    },
    getChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    getArticleInfo () {
      getArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    }
  }
}
</script>

