<template>
  <div>
    <el-card class="filter-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!--  -->
      <el-form :model="form" ref="form" label-width="100px" size="mini" :disabled="loading" v-loading="loading">
        <el-form-item label="">
          <el-radio-group v-model="status">
            <el-radio :label="null"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="channelsId" placeholder="">
            <el-option
            label=""
            :value="null"
            ></el-option>
            <el-option
            :label="item.name"
            :value="item.id"
            v-for="item in channelList"
            :key="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
          v-model="pubDate"
          type="datetimerange"
          start-placeholder=""
          end-placeholder=""
          :default-time="['12:00:00']"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="loadArticles(1)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span> {{totalCount}} </span>
      </div>
      <!--  -->
      <el-table
        :data="articles"
        style="width: 100%"
        stripe
        class="list-table"
        size="mini"
        v-loading="loading"
        >
        <el-table-column
          prop="date"
          label=""
        >
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.cover.images[0]"
            fit="cover"
            lazy
          >
            <!--  -->
            <div slot="placeholder" class="image-slot" style="text-align:center">
              <span class="dot">...</span>
            </div>
            <!--  -->
            <div slot="error" class="image-slot">
             <img src="./20.png" alt="">
            </div>
          </el-image>
          <!-- <img v-if="scope.row.cover.images[0]" :src="scope.row.cover.images[0]" class="article_cover">
          <img v-else src="./no-cover.gif" class="article_cover"> -->
        </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">{{articleStatus[scope.row.status].text}}</el-tag>
            <!-- <el-tag v-if="scope.row.status === 0"></el-tag>
            <el-tag v-if="scope.row.status === 1" type="info"></el-tag>
            <el-tag v-if="scope.row.status === 2" type="success"></el-tag>
            <el-tag v-if="scope.row.status === 3" type="warning"></el-tag>
            <el-tag v-if="scope.row.status === 4" type="danger"></el-tag> -->
          </template>
        </el-table-column>
        <el-table-column
          prop="pubdate"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              circle
              icon="el-icon-edit"
              type="primary"
              @click="$router.push(`/publish?id=${scope.row.id}`)"
              ></el-button>
            <el-button
              size="mini"
              type="danger"
              circle
              icon="el-icon-delete"
              @click="delArticle(scope.row.id)"
              ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        :total="totalCount"
        background
        @current-change="getCurrent"
        :disabled="loading"
        :current-page.sync="page"
        >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
// 
import { getArticles, getArticleChannels, delArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  data () {
    return {
      articles: [], // 
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articleStatus: [ // 
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '', type: '' },
        { status: 0, text: '', type: 'success' },
        { status: 0, text: '', type: 'warning' },
        { status: 0, text: '', type: 'danger' }
      ],
      page: 1, // 
      pageSize: 10, // 
      totalCount: 0, // 
      status: null, // 
      channelList: [], // 
      channelsId: null, // 
      pubDate: null, // 
      loading: true // 
    }
  },
  created () {
    this.loadArticles()
    this.loadArticleChannels()
  },
  methods: {
    loadArticles (page = 1) { // 
      // 
      this.loading = true
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelsId,
        begin_pubdate: this.pubDate ? this.pubDate[0] : null,
        end_pubdate: this.pubDate ? this.pubDate[1] : null
      }).then(res => {
        // 
        this.loading = false
        const { total_count: totalCount, results } = res.data.data
        this.articles = results
        this.totalCount = totalCount
      })
    },
    getCurrent (page) {
      this.loadArticles(page)
    },
    loadArticleChannels () {
      getArticleChannels().then(res => {
        this.channelList = res.data.data.channels
      })
    },
    // 
    delArticle (delId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delArticle(delId.toString()).then(res => {
          // 
          this.loadArticles(this.page)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.filter-card {
  margin-bottom: 30px;
}
.list-table {
  margin-bottom: 20px;
}
.article_cover {
  width: 60px;
}

</style>
