<template>
  <div class="settingsIndex">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div slot="header" class="clearfix">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item to="/"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
      </div>
      <el-col :span="12">
        <el-form
        ref="settings-form"
        :model="user"
        label-width="80px"
        :rules="settingsRules"
        >
          <el-form-item label="">
            {{user.id}}
          </el-form-item>
          <el-form-item label="">
            {{user.mobile}}
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="user.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="intro">
            <el-input type="textarea" v-model="user.intro"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="user.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
            type="primary"
            @click="onUpdateUserInfo"
            :loading="userInfoLoading"
            ></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col
      :span="12"
      style="text-align:center"
      >
        <label for="file">
          <el-avatar
          shape="square"
          :size="180"
          fit="cover"
          :src="user.photo"
          ></el-avatar>
          <p></p>
        </label>
        <input
        type="file"
        id="file"
        ref="file"
        hidden
        @change="onFileChange"
        >
      </el-col>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      @opened="omImageOpend"
      >
      <div class="imgContainer">
        <img
        :src="previewImage"
        class="previewImage"
        ref="image"
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
        type="primary"
        @click="updateUserPhoto"
        :loading="photoLoading"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 
import globalBus from '@/utils/global-bus'
// 
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
// 
import { getUserProfile, updatePhoto, updateUserInfo } from '@/api/user'
export default {
  name: 'settingsIndex',
  data () {
    return {
      // 
      settingsRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/, message: '', trigger: 'blur' }
        ]
      },
      user: {
        email: '',
        id: null,
        mobile: '',
        name: '',
        intro: '',
        photo: ''
      },
      previewImage: '',
      dialogVisible: false,
      cropper: '',
      photoLoading: false, // loading
      userInfoLoading: false
    }
  },
  created () {
    this.uploadUser()
  },
  methods: {
    onFileChange () {
      // 
      const file = this.$refs.file
      // 
      const blodUrl = window.URL.createObjectURL(file.files[0])
      // url
      this.previewImage = blodUrl
      // 
      this.dialogVisible = true
      // 
      file.value = ''
    },
    uploadUser () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    omImageOpend () {
      const image = this.$refs.image
      // 
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return
      }
      // cropper
      this.cropper = new Cropper(image, {
        viewMode: 1,
        dragMode: 'none',
        aspectRatio: 1,
        cropBoxResizable: false
        // 
        // crop (event) {}
      })
    },
    updateUserPhoto () {
      this.photoLoading = true
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        updatePhoto(fd).then(res => {
          this.photoLoading = false
          this.dialogVisible = false
          this.user.photo = window.URL.createObjectURL(file)
          // ,
          globalBus.$emit('user-info', this.user)
        })
      })
    },
    onUpdateUserInfo () {
      this.$refs['settings-form'].validate((valid) => {
        if (valid) {
          this.userInfoLoading = true
          const { name, intro, email } = this.user
          updateUserInfo({ name, intro, email }).then(res => {
            // ,
            globalBus.$emit('user-info', this.user)
            this.userInfoLoading = false
            this.$message({
              message: '',
              type: 'success'
            })
          })
        }
      })
    }
  }
}
</script>

<style scoped lang="less">
.previewImage {
  display: block;
  width: 100%;
  max-height: 200px;
}

</style>
